.text-box {
  text-align: center;
  position: absolute;
  top: 70px;
  left: 0;
  right: 0;
  height: 250px;
}
.text-box .text {
  font-size: 30px;
  color: #101010;
  font-weight: 500;
}
.cursor {
  animation: blink 0.5s infinite;
  font-size: 35px;
}

@keyframes blink {
  to {
    opacity: 0;
  }
}

.introduce {
  display: block;
  font-size: 20px;
  text-align: center;
  color: #444444;
  font-weight: 400;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
}
#about-lable{
    margin-top: 80px;
    box-sizing: border-box;
}
.about-box {
    
    height: 520px;
  position: relative;
}
.profile-box {
  width: calc(100% - 400px);
  padding: 8px 20px;
  margin: 50px auto 70px;
  box-sizing: border-box;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.25);
}
.project-box .btn-box{
    display: flex;
    align-items: center;
    column-gap: 10px;
    position: absolute;
    transition: all 700ms;
    bottom: -100%;
    left: 10px;
}
.project-box:hover .btn-box{
   
    transition: all 700ms;
    bottom: 10px;
}
.profile-box .img-inform {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
}
.profile-box .img-inform > img {
  width: 300px;
  height: auto;
  border-radius: 50%;
}
.profile-box .img-inform span {
  font-size: 20px;
}
.profile-box .item-icon {
  width: 40px;
  height: 40px;
}
.profile-box .img-inform ul {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}
.profile-box .img-inform li {
  display: flex;
  align-items: center;
  column-gap: 20px;
}
.profile-box .img-inform li.pointer{
 animation: pointerAni 4000ms infinite  linear;
}
.profile-box .img-inform li.pointer:hover{
    animation: none;
}
@keyframes pointerAni {
    0%{
        opacity: 0;
    }
    
    50%{
        opacity: 1;

    }
    100%{
        opacity: 0;
    }
}
.skills-content {
  padding: 20px;
}
.skill-box {
  display: grid;
  gap: 40px 20px;
  grid-template-columns: repeat(4, 1fr);
}
.skill-box li {
  display: flex;
  align-items: center;
  cursor: pointer;
  flex-direction: column;
  row-gap: 15px;
}
.skill-box span {
  font-size: 20px;
}
.skill-box .item-box img {
  width: 150px;
  height: 150px;
}
.skill-box .item-box > * {
  position: absolute;
  width: 150px;
  height: 150px;
  backface-visibility: hidden; /* 추가 */
}
.skill-box .item-card {
  perspective: 1100px;
  width: auto;
  height: auto;
}
.skill-box .item-box {
  position: relative;
  transform-style: preserve-3d;
  width: 150px;
  height: 150px;
  display: inline-block;
  transition: transform 0.3s; /* 추가 */
  transform: perspective(800px) rotateY(0deg); /* 추가 */
}
.skill-box .item-card:hover {
  transform: perspective(800px) rotateY(180deg); /* 추가 */
}
.skill-box .item-card:hover .item-back {
  transform: rotateY(180deg);
  display: flex;
  align-items: center;
  justify-content: center;
}
.skill-box .item-card:hover .item-box {
  transform: rotateY(180deg);
}
.skill-box .item-back {
  display: none;
}

.skill-box .item-back span {
  transform: rotateY(180deg);
  font-size: 30px;
  color: #F280CA;
}
.profile-box.projects-box,
.profile-box.experience-box{
    display: grid;
    box-shadow: none;
    background-color: transparent;
    padding: 0;
    row-gap: 30px;
    justify-content: space-between;
    grid-template-columns: repeat(3, 270px);
}
.project-box{
    width: 270px;
    background-color: #fff;
    box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.25);
    height: 270px;
    border-radius: 10px;
    padding: 10px;
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
}
.project-box img{
    width: 100%;
    height: 100%;
    margin: auto;
}
.project-box > *{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.project-box .item{
    width: 100%;
    border-radius: 10px;
    /* height: 100%; */
    padding: 10px;
    height: 0%;
    opacity: 0;
    box-sizing: border-box;
    top: unset;
    transition: all 700ms;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
}
.project-box:hover .item{
    padding: 10px;
        height: 100%;
    opacity: 1;
    transition: all 700ms;
}
.project-box .title-btn{
    align-items: center;
    justify-content: space-between;
    display: flex;
}
.project-box .inform-box{
    display: flex;
    margin-top: 10px;
    row-gap: 5px;
    flex-direction: column;
}
.project-box .inform-box .position-text{
    white-space: nowrap;
}
.project-box .inform-box span{
    color: #e8e8e8;
    font-size: 14px;
}
.star-item{
    top: -75px;
    left: 0px;
}
.star-item {
    position: relative;
    position: absolute;
    display: block;
    width: 150px;
    border-radius: 50%;
    height: 150px;
    transform: rotate(35deg);
    animation: starTop 8000ms infinite forwards linear;
  }

  @keyframes starTop {
    to{
        top: calc(100% + 50px);
    }
    
    from{
        top: -75px;
    }
  }
.contact-box{
    display: flex;
    column-gap: 25px;
    align-items: center;
}
.contact-box > div{
    width: 50%;
    height: fit-content;
}
.contact-right form{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 15px;
}
.contact-right form textarea,
.contact-right form input{
    border: 1px solid #bcbcbc;
    box-sizing: border-box;
    width: 100%;
    height: 35px;
    padding: 0 7px;
    border-radius: 3px;
    color :#101010;
    font-size: 18px;
    caret-color: #F280CA;
}
.contact-right form textarea:focus,
.contact-right form input:focus{
    outline: none;
}
.contact-right form textarea{
    height: 200px;
    padding:7px;
    resize: none;
}
.contact-right form span{
    font-size: 25px;
}
.contact-right form input[type='submit']{
    background-color: #fff;
}
.contact-left{
    width: 50%;
    overflow: hidden;
    border-radius: 8px;
}
.contact-left img{
    width: 100%;
}